import React, { Component } from 'react'; //引入react组件
import "../css/search.css";
import $ from 'jquery';

class Search extends React.Component {
	constructor() {
		super()
		this.state = {
			arr: [],
			inpVal: '',
			valArr: []
		}
	}
	componentDidMount() {
		this.res = $.get(this.props.source, function (res) {
			console.log(res.seach)
			this.setState({
				arr: res.seach,
			})
		}.bind(this))
	}
	change() {
		if ($('input').val() === '') {
			$('.m-recom').hide();
			$('.m-hotlist').show();
			$('.u-svg-empty').hide();
			$('.holder').show();
			this.setState({
				valArr: []
			})
		} else {
			$('.holder').hide();
			$('.u-svg-empty').css('display', 'inline-block');
			$('.m-hotlist').hide();
			$('.m-recom').show();
			var val =$('input').val();
			this.res = $.get(this.props.source,function (res) {
				res.result.map((item)=>{
					if($.inArray(val,item)===0){
						this.setState({
								inpVal: $('input').val(),
								valArr: item
							});
						}
				});
		}.bind(this))
		}
	}
	close() {
		$('.m-hotlist').show();
		$('.m-recom').hide();
		$('input').val('');
		$('.u-svg-empty').hide();
		$('.holder').show();
		this.setState({
			valArr: []
		})
	}
	render() {
		return <div className="m-hmsrch">
			<form className="m-input f-bd f-bd-btm" method="get" action="#">
				<div className="inputcover">
					<i className="u-svg u-svg-srch"></i>
					<input type="text" name="search" className="input" autoComplete="off" onInput={this.change.bind(this)} />
					<label className="holder">搜索歌曲、歌手、专辑</label>
					<figure className="close" onClick={this.close.bind(this)}>
						<i className="u-svg u-svg-empty"></i>
					</figure>
				</div>
			</form>

			<div className="m-default">
				<section className="m-hotlist">
					<h3 className="title">热门搜索</h3>
					<ul className="list">
						{this.state.arr.map((item) => {
							return <li className="item f-bd f-bd-full">
								<a className="link" href="www.baidu.com">{item}</a>
							</li>
						})}
					</ul>
				</section>

				<section className="m-recom">
					<h3 className="title f-bd f-bd-btm f-thide">搜索“{this.state.inpVal}”</h3>
					<ul>
						{this.state.valArr.map((item) => {
							return <li className="recomitem">
							<i className="u-svg u-svg-search">
								</i>
								<span className="f-bd f-bd-btm f-thide">{item}</span>							
							</li>
						})}
					</ul>
				</section>
			</div>
		</div>

	}
}
Search.defaultProps = {
	source: 'https://xuyouwg.github.io/react-demo/data.json',
}
export default Search;  