import React, { Component } from 'react'; //引入react组件
import '../css/home.css';
import $ from 'jquery';

class Home extends React.Component {
	constructor() {
		super()
		this.state = {
			arr: [],
			arr1: []
		}
	}
	componentDidMount() {
		this.res = $.get(this.props.source, function (res) {
			console.log(res.newMusic)
			this.setState({
				arr: res.recommend,
				arr1: res.newMusic
			})
		}.bind(this))
	}
	render() {
		return <div className='tabctitem'>
			<div className='m-homeremd'>
				<div className="remd_tl">推荐歌单</div>
				<div className="remd_songs">
					<div className="remd_ul">
						{this.state.arr.map((item) => {
							return <a className="remd_li" href={"https://music.163.com/m/playlist?id="+item.id}>
								<div className="remd_img">
									<img className="u-img" src={item.imgsrc} alt="#" />
									<span className="u-earp remd_lnum">{item.num}</span>
								</div>
								<div className="remd_text">{item.text}</div>
							</a>
						})}
					</div>
				</div>

				<div className="remd_tl">最新音乐</div>
				<div className="remd_newsg">
					<div className="m-sglst">
						{this.state.arr1.map((item) => {
							return <div className="m-sgitem" >
								<a className="sgfr f-bd f-bd-btm" href={"https://music.163.com/m/song?id="+item.id}>
									<div className="sgchfl">
										<div className="f-thide sgtl">{item.thide}<span className="sgalia">{item.sgalia}</span>
										</div>
										<div className="f-thide sginfo">
											<i className="u-hmsprt sghot"></i>{item.sginfo}</div>
									</div>
									<div className="sgchfr">
										<span className="u-hmsprt sgchply">
										</span>
									</div>
								</a>
							</div>
						})}
					</div>
				</div>

				<footer className="m-homeft">
					<div className="ftwrap">
						<div className="logo">
							<img src="https://xuyouwg.github.io/react-demo/images/home.svg" className="logosvg" alt="logo" />
						</div>
						<div className="openapp">打开APP，发现更多好音乐 &gt;</div>
						<p className="copyright">网易公司版权所有©1997-2018   杭州乐读科技有限公司运营</p>
					</div>
				</footer>
			</div>
		</div>
	}
}
Home.defaultProps = {
	source: 'https://xuyouwg.github.io/react-demo/data.json',
}
export default Home;  
