import React, { Component } from 'react'; //引入react组件
import "../css/home1.css";
import $ from 'jquery';

class Home1 extends React.Component {
	constructor() {
		super()
		this.state = {
			arr: []
		}
	}
	componentDidMount() {
		this.res = $.get(this.props.source, function (res) {
			console.log(res.hotMusic)
			this.setState({
				arr: res.hotMusic
			})
		}.bind(this))
	}
	render() {
		return <div className="m-tabct">
			<div className="tabctitem">
				<div className="m-hmhot">
					<div className="hotop">
						<div className="hotopct">
							<div className="u-hmsprt hoticon">
							</div>
							<div className="hottime">更新日期：10月25日</div>
						</div>
					</div>

					<div className="hotcont">
						<div className="m-sglst">
							{this.state.arr.map((item, index) => {
								return <a className="m-sgitem" href="https://music.163.com/m/song?id=486814412">
									<div className="sgfl">{index < 9 ? "0" + (index + 1) : (index + 1)}
									</div>
									<div className="sgfr f-bd f-bd-btm">
										<div className="sgchfl">
											<div className="f-thide sgtl">{item.sgtl}<span className="sgalia">{item.sgalia}</span></div>
											<div className="f-thide sginfo">
												<i className={item.sghot}></i>{item.info}
											</div>
										</div>
										<div className="sgchfr">
											<span className="u-hmsprt sgchply"></span>
										</div>
									</div>
								</a>
							})}
						</div>
					</div>

					<div className="hotdn">
						<span className="hotview">查看完整榜单</span>
					</div>
				</div>
			</div>
		</div>
	}
}
Home1.defaultProps = {
	source: 'https://xuyouwg.github.io/react-demo/data.json',
}
export default Home1;  
